<template>
  <div class="game-detail">
    <div class="main-box">
      <img :src="wardList.icon" alt />
      <div>
        <p>{{wardList.pagName}}</p>
        <p>
          <!-- <span>10万+下载</span> -->
          <span class="size">{{wardList.pagRemark}}</span>
        </p>
      </div>
    </div>
    <h3>礼包类型</h3>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      class="van-clearfix"
    >
      <van-cell v-for="item in wardList.awardList" :key="item.awardId">
        <div class="left-box">
          <h4>{{item.awardName}}</h4>
          <p @click="foldText($event)" class="fold">{{item.remark}}</p>
        </div>
        <van-button round color="#f1f0f6" @click="getGift(item.awardId)">领取</van-button>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
import { List, Cell, Button, Dialog } from "vant";
import IndexLess from "./index.less";
import common from "../../style/common.less";
import axios from "axios";
import { Toast } from "vant";
import {goToLogin} from  '@/untils/loginCheck'
export default {
  components: {
    [List.name]: List,
    [Button.name]: Button,
    [Cell.name]: Cell
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      fold: true,
      wardList: {
        icon: "",
        pagRemark: "",
        pagName: ""
      }
    };
  },
    mounted () {
        goToLogin()
    },
  methods: {
    onLoad() {
      // 异步更新数据
      this.loading = true;
      axios
        .post(process.env.VUE_APP_API + "packageAwardList.do", {
          activityId: 190822080,
          pagAwardId: this.$route.query.id
        })
        .then(response => {
          console.log("response", this.list);
          this.wardList = response.data.data;
          console.log("wardList", this.wardList);
          if (this.list.length == 0) {
            this.list = response.data.data;
          } else {
            response.data.data.forEach((item, index, array) => {
              this.list.push(item);
            });
          }
          // 加载状态结束
          this.loading = false;
          // 数据全部加载完成
          this.finished = true;
        })
        .catch(function(error) {
          console.log(error);
        });
      // setTimeout(() => {
      //   for (let i = 0; i < 10; i++) {
      //     this.list.push(this.list.length + 1);
      //   }

      // }, 500);
    },
    foldText(e) {
      let element = e.currentTarget;
      if (element.classList.contains("fold")) {
        element.classList.remove("fold");
        element.classList.toggle("unfold");
      } else {
        element.classList.toggle("fold");
        element.classList.remove("unfold");
      }
    },
    getGift(item) {
      axios
        .post(process.env.VUE_APP_API + "/getAward.do", {
          phone: this.encrypt(13798163108),
          activityId: 190822080,
          awardId: item
        })
        .then(response => {
          if (response.data.resultCode == 0) {
            Dialog.confirm({
              title: "领取成功",
              message: `<P>你已经成功领取"${response.data.data.awardName}"</P>
                      <div class="code-box"><span>激活码</span><span class="code">${response.data.data.awardCode}</span></div>
            `,
              cancelButtonText: "关闭",
              confirmButtonText: "复制"
            })
              .then(() => {
                this.copy(response.data.data);
                // on confirm
              })
              .catch(() => {
                // on cancel
              });
          } else {
            Toast({
              message: response.data.msg,
              position: "center",
              duration: 1000
            });
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    copy(res) {
      let clipboard = new this.Clipboard(".van-dialog__confirm", {
        text: function() {
          return res.awardCode;
        }
      });
      clipboard.on("success", e => {
        Toast({
          message: "激活码已复制",
          position: "center",
          duration: 1000
        });
        // 释放内存
        clipboard.destroy();
      });
      clipboard.on("error", e => {
        // 不支持复制
        Toast({
          message: "复制失败,请手动复制",
          position: "center",
          duration: 1000
        });
        // 释放内存
        clipboard.destroy();
      });
    }
  },

};
</script>
<style lang="less">
.main-box {
  height: 80px;
  padding: 12px;
  border-bottom: 1px solid #e6e6e6;
  img {
    width: 56px;
    height: 56px;
    border-radius: 12.5px;
    float: left;
    margin: 12px 9px 12px 0;
  }
  div {
    float: left;
    .size {
      margin-left: 9px;
    }
  }
}
h3 {
  font-size: 11px;
  color: #4e4e4e;
  font-weight: normal;
  margin: 12px 0 0 12px;
}
.left-box {
  float: left;
  width: 250px;
  overflow: hidden;
  h4 {
    font-size: 15px;
    color: #000;
    margin: 0;
    font-weight: normal;
  }
  p {
    margin: 0;
    font-size: 11px;
    color: #4e4e4e;
  }
  .fold {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .unfold {
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
    margin-top: 12px;
    line-height: 20px;
  }
}
.van-dialog {
  width: 245px;
  height: 168px;
  .van-dialog__header {
    padding: 17px;
  }
  .van-dialog__message--has-title {
    height: 65px;
    padding: 0;
    white-space: normal;
    text-align: left;
    margin-left: 30px;
    p {
      margin: 0;
    }
    span {
      display: inline-block;
    }
    .code {
      width: 130px;
      height: 29px;
      margin-left: 9px;
      border-radius: 14.5px;
      background: #f1f1f1;
      text-align: center;
    }
    .code-box {
      height: 29px;
      margin-top: 12px;
      line-height: 29px;
      color: #000;
    }
  }
  .van-dialog__footer {
    margin-top: 9px;
    .van-button {
      height: 39px;
      margin-top: 0;
      background-color: #fff;
    }
    .van-button:first-of-type {
      color: #000;
    }
  }
}
</style>